.toolbar {
  list-style-type: none;
  height: 7.5vh;
  font-size: 1.8rem;
  position: fixed;
  width: 100%;
  z-index: 10000;
  transition: 0.2s;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0);
}

.toolbar__navigation {
  display: -webkit-flex;
  display: flex;
  color: white;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 1rem;
}

.toolbar__logo {
  margin-left: 1rem;
}

.toolbar__logo a {
  font-weight: bolder;
  font-size: larger;
  color: white;
  padding: 0;
  font-size: 2.4rem;
  font-family: "Permanent Marker", cursive;
  letter-spacing: 6px;
}

.toolbar__logo a:hover,
.toolbar__logo a:active {
  text-decoration: none;
}

.toolbar__logo a span {
  color: var(--secondary-color);
}

.spacer {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
}

.toolbar_navigation-items li {
  list-style: none;
  padding: 0;
  transition: 0.5s;
  margin: 0 2rem;
}

.toolbar_navigation-items ul li a {
  margin: 6px;
  color: var(--white);
  display: block;
  position: relative;
  font-family: "Rajdhani", sans-serif;
  font-size: 1.8rem;
  font-weight: 600;
  text-decoration: none;

  transition: 0.5s;
}

.toolbar_navigation-items ul li a:before {
  content: "";
  width: 0;
  border-radius: 2px;
  height: 10%;
  position: absolute;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(
    to right,
    #00ffff,
    #26fff5,
    #3dffea,
    #50ffde,
    #61ffd3,
    #73ffc6,
    #85ffb8,
    #97ffab,
    #afff9a,
    #c7fe8a,
    #e0fb7c,
    #f9f871
  );
  transition: width 0.5s;
}

.toolbar_navigation-items ul li a:hover:before,
.toolbar_navigation-items ul li a:focus:before {
